import React, { useState } from 'react';
import dayjs from 'utils/utc';

import axios from 'utils/axios'
import Page from "../../components/Page";
import ListPage from 'components/ListPage'
import { getColumnSearchProps } from 'utils/columnSearch';
import {Link} from "react-router-dom";
import {Button, message, Popconfirm ,DatePicker} from "antd";
import EditForm from "./EditForm";
import {addSecurityList, DelSecurityList,kickOutSecurityUser} from "../../api/sys";
import locale from "antd/es/date-picker/locale/zh_CN";

const { RangePicker } = DatePicker;
export default (props) => {
  const [dataSource, setDataSource] = useState({total: 0, data: []});
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(false);
  const [fromData, setFromData] = useState([]);

  const fetchData = async params => {
    let resp = await axios.get('/security/list', {params});
    if (resp.code !== 0) return ;

    let data = resp.data;
    let dataList = data.data;
    // console.log("ddddd", resp);
    setDataSource({total: data.total, data: dataList})
  };

  //
  const edit = (record, index) => {
    setFromData(record);
    setVisible(true);
  };
  const onSubmit = async fixData => {
    // console.log("", fixData);
    setLoading(true)

      let resp = await addSecurityList({ ...fixData })
      setLoading(false)
      if (resp.code === 0) {
        // setData([{ ...fixData, id: resp.data.id, key: resp.data.id ,log_time: resp.data.log_time}, ...data])
        setDataSource( {total: dataSource.total,data: [{ ...fixData, id: resp.data.id, key: resp.data.id ,expire_time: resp.data.expire_time}, ...dataSource.data]})
      } else {
        message.error(resp.err);
        return
      }

    setVisible(false);
  }

  const handleDelete = async record => {
    const resp = await DelSecurityList( record.id ,record);
    if (resp.code !== 0) {
      message.error(resp.err);
      return
    }
    const list = [...dataSource.data];
    setDataSource({total: dataSource.total, data: list.filter(item => item.id !== record.id)})
  };

  const handleKickOut = async record => {
    const resp = await kickOutSecurityUser( record.id ,record);
    if (resp.code !== 0) {
      message.error(resp.err);
      return
    }
    const list = [...dataSource.data];
    // setData(list.filter(item => item.id !== record.id));
  };

  function rangeChange(dates, dateStrings) {
    // searchParams["times"]=dateStrings
  }



  const columns = [
    { title: '', dataIndex: 'id' },
    { title: 'UID', dataIndex: 'uid', key: 'uid', ...getColumnSearchProps('uid'),
      render: user => <Link to={`/user/userInfo?uid=${user}`}>{user}</Link>
    },
    { title: 'UID', key: 'with_uid', dataIndex: 'with_uid',
      render: with_uid => <Link to={`/user/userInfo?uid=${with_uid}`}>{with_uid}</Link>
    },
    { title: '', key: 'expire_time', dataIndex: 'expire_time', render: time => dayjs.unix(time).utc().utcOffset(330).format('YYYY-MM-DD HH:mm:ss')},
    { title: '', key: 'reason', dataIndex: 'reason',
      render: reason => {
        const flagMap = {
          1: "",
          2: "",
          3: "",
          4: "",
        };
        return flagMap[reason];
      }
    },
    {
      title: 'Operation', dataIndex: 'operation', align: 'center',
      render: (text, record, index) => {
        return (
            <span>
                <Popconfirm title="Sure to kick out?" onConfirm={() => handleDelete(record)}>
              <Button type="link"></Button>
            </Popconfirm>
            <Popconfirm title="Sure to delete?" onConfirm={() => handleKickOut(record)}>
              <Button type="link"></Button>
            </Popconfirm>
          </span>
        )
      }
    },

  ];

  return <Page title="">
    <Button type="primary" onClick={() => edit()}></Button>

    {/*<RangePicker*/}
        {/*defaultValue={[dayjs().startOf('day'), dayjs().endOf('day')]}*/}
        {/*format={"YYYY-MM-DD HH:mm:ss"}*/}
        {/*showTime={locale}*/}
        {/*onChange={rangeChange}*/}
    {/*/>*/}
    <ListPage
      columns={columns}
      fetchApi={fetchData}
      data={dataSource.data}
      total={dataSource.total}
      pageSize={20}
      // rangeSearch={{showTime: true}}
      showTotal={(total, range) => `${range[0]}-${range[1]}, ${total}`}
    />

    <EditForm
        data={fromData}
        visible={visible}
        onOk={onSubmit}
        onCancel={() => {
          setVisible(false);
        }}
    />
  </Page>;
}
