
import React, { useState, useEffect } from 'react';
import { Modal, Form, Input, Select, Button, Space,InputNumber } from 'antd';
import DatePicker from 'components/DatePicker';
import locale from 'antd/es/date-picker/locale/zh_CN';
import { MinusCircleOutlined } from '@ant-design/icons';
import State from 'constants/state';
import dayjs from "../Online";

function EditForm(props) {
  const { TextArea } = Input;
  const { Option } = Select;
  const { RangePicker } = DatePicker;

  const { data, visible, onCancel, onOk } = props;

  const [defaultData, setDefault] = useState([]);

  const [form] = Form.useForm();

  useEffect(() => {
    setDefault(data);
  }, [data]);

  useEffect(() => {
    console.log("Form data initial", defaultData);
    form.resetFields();
  }, [defaultData]);

  const submit = () => {
    const submitData = form.getFieldsValue();
    console.log("submitData:",submitData);

    const fixData = { ...data, ...submitData };
    fixData.expire_time = fixData.expire_time.unix();
    onOk(fixData);
  };

  return (
    <Modal visible={visible} title="" maskClosable={false} closable={false}
      getContainer={false} footer={null}
    >
      <Form form={form} name="form_in_modal" initialValues={defaultData} onFinish={submit}>

        <Form.Item label='Uid' name='uid' rules={[{ required: true, message: 'Uid!' }]}>
          <Input style={{ width: '60%' }}/>
        </Form.Item>
        <Form.Item label='Uid' name='with_uid' rules={[{ required: true, message: 'Uid' }]}>
          <Input style={{ width: '60%' }}/>
        </Form.Item>
        <Form.Item label='' name='reason' rules={[{ required: true, message: '' }]}>
          {/*<TextArea style={{ width: '100%', height:'300px' }} />*/}
          <Select style={{ width: '60%' }}>
            <Select.Option value={1}></Select.Option>
            <Select.Option value={2}></Select.Option>
            <Select.Option value={3}></Select.Option>
            <Select.Option value={4}></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item label='' name='expire_time' rules={[{ required: true, message: '' }]}>
          {/*<InputNumber style={{ width: '60%' }}/>*/}
          <DatePicker locale={locale}  showTime format="YYYY-MM-DD HH:mm:ss"/>
        </Form.Item>

        <Form.Item >
          <Space>
            <Button type="primary" htmlType="submit" ></Button>
            <Button htmlType="button" onClick={onCancel}></Button>
          </Space>
        </Form.Item>

      </Form>
    </Modal >
  );
}
export default EditForm;
