import React from 'react';
import { useSelector } from "react-redux";
import { Select } from "antd";

const { Option } = Select;

function Index(props) {

  const { handleChange } = props;
  const appidList = useSelector(state => state.appids);

  return (
    <div style={{padding: '10px 0'}}>
      <Select defaultValue="Choose a channel..." style={{ width: 300 }} onChange={handleChange} loading={appidList.length < 1}>
        {appidList.map(v =>
          <Option key={v.id} value={v.appid}>{v.appName}</Option>
        )}
      </Select>
    </div>
  );
}

export default Index;
